<template>
    <div class="money-table">
        <table class="ac">
            <thead>
            <tr>
                <td>来源/用途</td>
                <td>金额变化</td>
                <td>时间</td>
                <td style="width: 200px">备注</td>
            </tr>
            </thead>
            <tbody>
            <tr v-for="(vo , index) in log">

                <td v-if="vo.fromtype == 1">消费购买</td>
                <td v-if="vo.fromtype == 2">系统发放</td>
                <td v-if="vo.fromtype == 4">取消订单</td>
                <td v-if="vo.fromtype == 5">系统调整</td>
                <td v-if="vo.fromtype == 6">朋友赠送</td>
                <td v-if="vo.fromtype == 7">企业回收</td>
                <td v-if="vo.fromtype == 8">充值卡充值</td>
                <td v-if="vo.fromtype == 9">支付宝/微信充值</td>
                <td v-if="vo.fromtype == 10">充值卡剩余积分</td>
                <td v-if="vo.fromtype == 11">退货订单</td>

                <td :class="['green','ac','bold',{red:vo.type == 1}]"><span v-if="vo.type ==1">{{'+'}}</span>{{vo.point | increase(vo.type) | change_unit}}</td>
                <td>{{vo.add_time | format}}</td>
                <td class="al" style="padding: 10px 5px">{{vo.note}}</td>
                <!--<td v-if="vo.type == 1">增加</td>-->
                <!--<td v-if="vo.type == 2">减少</td>-->

            </tr>
            </tbody>
        </table>
    </div>
</template>

<script>
    export default {
        name: "money-table",
        props:{
            log:{
                type:Array,
                default:[]
            }
        }
    }
</script>

<style scoped lang="stylus">
    .money-table
        margin-top 10px
        position relative
        table
            width 100%
            thead
                height 30px
                line-height 30px
                background-color #e4e4e4
            tbody
                background-color white
                tr
                    background-color white
                    height 40px
                    border-bottom 1px solid #ccc
                    &:last-child
                        border-bottom none
                .green
                    color green
                .red
                    color red
</style>
