<template>
    <div class="money">
        <div class="money-preview row">
            <div class="col-4">
                <p>我的余额 :<span style="font-size: 28px;font-weight: bold;color: #ff0077;padding: 0 5px;">{{storage.getStorage('fft-user-info').point | change_unit}}</span> {{storage.getStorage('exchange').unit}}
                </p>
            </div>
            <div class="col-6 row">
                <div class="col-4 ac">
                    <router-link class="money-preview-btn" to="money/present">余额转赠</router-link>
                </div>
                <div class="col-4 ac">
                    <router-link class="money-preview-btn" to="money/exchange">卡券兑换</router-link>
                </div>
                <div class="col-4 ac">
                    <a class="money-preview-btn" :href="`/grade.html?can=1`">我能购买</a>
                </div>
            </div>
        </div>
        <div class="money-filter clearfix">
            <div class="money-filter-item cursor fl ac" :class="{active:tag=== -1}"><a @click="getLog(-1)">全部明细</a></div>
            <div class="money-filter-item cursor fl ac" :class="{active:tag=== 1}"><a @click="getLog(1)">余额收入</a></div>
            <div class="money-filter-item cursor fl ac" :class="{active:tag=== 2}"><a class="no-after" @click="getLog(2)">余额支出</a></div>
            <div class="money-filter-item cursor fr ac"><a class="no-after" @click="PageTurning(1)">下一页</a></div>
            <div class="money-filter-item cursor fr ac"><a  @click="PageTurning(-1)">上一页</a></div>
        </div>
        <!--<div class="money-selector">-->
            <!--<select name="select" id="setmoney" @change="changed()">-->
                <!--<option value="1" class="ac">最近一周</option>-->
                <!--<option value="2" class="ac">最近一个月</option>-->
                <!--<option value="3" class="ac">最近三个月</option>-->
            <!--</select>-->
        <!--</div>-->
        <MoneyTable :log="log.slice(page*limit,(page+1)*limit)"></MoneyTable>
    </div>
</template>

<script>
    let display = {
        showRecommend: true,
        nav: 'default',
        header: 'default'
    };
    import MoneyTable from '../components/money-table'

    export default {
        name: "money",
        metaInfo: {
            title: '我的余额-个人中心', // set a title
            meta: [{                 // set meta
                name: '福分通',
                content: '专注企业员工福利'
            }]
        },
        components: {
            MoneyTable
        },
        created() {
            this.$store.dispatch('layout/Change', display);
            this.getLog(this.tag);
        },
        methods: {
            PageTurning(a){
                if (a>0){
                    this.page===this.TotalPage?this.page=this.page:this.page+=a;
                }else{
                    this.page===0?this.page=0:this.page+=a
                }
            },
            getLog(flag){
                this.tag = flag;
                this.bus.$emit('loading','加载中',false);
                this.yum('api/member_point/pointLog',{type:flag}).then(res => {
                    if (res.data.status === 200) {
                        setTimeout(()=>{
                            this.bus.$emit('loading','加载中',true);
                        },300);
                        this.log = res.data.data;
                    }else {
                        setTimeout(()=>{
                            this.bus.$emit('notice',res.data.msg);
                            this.bus.$emit('loading','加载中',true);
                        },300)
                    }
                }).catch(err=>{
                    this.bus.$emit('notice','网络错误！');
                })
            }
        },
        data() {
            return {
                tag:-1,
                log: [],
                page:0,
                limit:10
            }
        },
        computed:{
            TotalPage(){
                let page = Math.ceil(this.log.length/this.limit)-1;
                return page
            }
        }
    }
</script>

<style scoped lang="stylus">
    $shadow = 0 0 8px #bbb
    $ThemeColor = #ff0077

    .money-preview
        height 100px
        line-height 100px
        background-color white
        box-shadow 0 0 8px #bbb
        & > .col-4
            border-right 1px solid #ccc
            height inherit
            padding 30px 0 0 20px
            p
                height 25px
                line-height 25px
        &-btn
            padding 7px 15px
            border 1px solid #555555
            border-radius 16px
            &:hover
                background-color #ff0077
                border-color #ff0077
                color white

    .money-filter
        height 50px
        margin-top 10px
        box-shadow $shadow
        background-color white
        &-item
            width 100px
            height 50px
            line-height 48px
            position relative
            user-select none
            &:hover,&.active
                border-bottom 2px solid #ff0077
                a
                    color #ff0077
                    font-weight bold
            a
                width 100%
                display block
                &:after
                    content ''
                    display inline-block
                    width 0
                    height 13px
                    transform translate(22px, 1px)
                    border-right 1px solid #ccc
                &.no-after:after
                    display none

    .money-selector
        margin-top 10px
        #setmoney
            width 120px
            height 30px
</style>
